@import url("https://fonts.googleapis.com/css2?family=Righteous&display=swap");
@mixin insecte {
    background: linear-gradient(180deg, #729f3f 50%, #729f3f 50%);
    background-color: #729f3f;
    color: #fff;
}
@mixin dragon {
    background: linear-gradient(180deg, #53a4cf 50%, #f16e57 50%);
    background-color: #53a4cf;
    color: #fff;
}
@mixin fée {
    background: linear-gradient(180deg, #fdb9e9 50%, #fdb9e9 50%);
    background-color: #fdb9e9;
    color: #212121;
}
@mixin feu {
    background: linear-gradient(180deg, #fd7d24 50%, #fd7d24 50%);
    background-color: #fd7d24;
    color: #fff;
}
@mixin spectre {
    background: linear-gradient(180deg, #7b62a3 50%, #7b62a3 50%);
    background-color: #7b62a3;
    color: #fff;
}
@mixin sol {
    background: linear-gradient(180deg, #f7de3f 50%, #ab9842 50%);
    background-color: #f7de3f;
    color: #212121;
}
@mixin normal {
    background: linear-gradient(180deg, #a4acaf 50%, #a4acaf 50%);
    background-color: #a4acaf;
    color: #212121;
}
@mixin psy {
    background: linear-gradient(180deg, #f366b9 50%, #f366b9 50%);
    background-color: #f366b9;
    color: #fff;
}
@mixin acier {
    background: linear-gradient(180deg, #9eb7b8 50%, #9eb7b8 50%);
    background-color: #9eb7b8;
    color: #212121;
}
@mixin ténèbres {
    background: linear-gradient(180deg, #707070 50%, #707070 50%);
    background-color: #707070;
    color: #fff;
}
@mixin électrik {
    background: linear-gradient(180deg, #eed535 50%, #eed535 50%);
    background-color: #eed535;
    color: #212121;
}
@mixin combat {
    background: linear-gradient(180deg, #d56723 50%, #d56723 50%);
    background-color: #d56723;
    color: #fff;
}
@mixin vol {
    background: linear-gradient(180deg, #3dc7ef 50%, #bdb9b8 50%);
    background-color: #3dc7ef;
    color: #212121;
}
@mixin plante {
    background: linear-gradient(180deg, #9bcc50 50%, #9bcc50 50%);
    background-color: #9bcc50;
    color: #212121;
}
@mixin glace {
    background: linear-gradient(180deg, #51c4e7 50%, #51c4e7 50%);
    background-color: #51c4e7;
    color: #212121;
}
@mixin poison {
    background: linear-gradient(180deg, #b97fc9 50%, #b97fc9 50%);
    background-color: #b97fc9;
    color: #fff;
}
@mixin roche {
    background: linear-gradient(180deg, #a38c21 50%, #a38c21 50%);
    background-color: #a38c21;
    color: #fff;
}
@mixin eau {
    background: linear-gradient(180deg, #4592c4 50%, #4592c4 50%);
    background-color: #4592c4;
    color: #fff;
}
* {
    margin: 0;
    padding: 0;
    font-family: Righteous, Arial, Helvetica, sans-serif;
    text-decoration: none;
}
body {
    background: linear-gradient(rgba(146, 146, 146, 0.4), rgba(146, 146, 146, 0.4)),
        url("../../public/assets/body_background.png");
}
.container {
    max-width: 1200px;
    margin: auto;
    background-color: #e4e4e4;
}
h4,
h3 {
    letter-spacing: 2px;
}
h3 {
    font-size: 1.5rem;

    @media screen and( max-width:800px) {
        font-size: 1.2rem;
    }
    @media screen and( max-width:400px) {
        font-size: 1rem; 

    }
}
.pill {
    border-radius: 5px;
    line-height: 30px;
    letter-spacing: 1.2px;
    max-width: 110px;
    margin: 1.6% 1.6% 0 0;
    width: 40%;
    float: left;
    font-size: 1rem;
    text-align: center;

    @media screen and(max-width:500px){
        width: 30%; 
    }
}
.insecte {
    @include insecte;
}

.dragon {
    @include dragon;
}

.fée {
    @include fée;
}

.feu {
    @include feu;
}

.spectre {
    @include spectre;
}

.sol {
    @include sol;
}

.normal {
    @include normal;
}

.psy {
    @include psy;
}

.acier {
    @include acier;
}

.ténèbres {
    @include ténèbres;
}

.électrik {
    @include électrik;
}

.combat {
    @include combat;
}

.vol {
    @include vol;
}

.plante {
    @include plante;
}

.glace {
    @include glace;
}

.poison {
    @include poison;
}

.roche {
    @include roche;
}

.eau {
    @include eau;
}
